<template>
	<div class="inline-flex">
		<div
			v-for="(item, itemIndex) of items"
			:key="itemIndex"
			class="inline-flex">
			<slot :item="item">
				<div :class="item.classes" @click="handleSelect(by ? item[by] : null)">
					<slot :item="item" name="label">
						{{ name ? item[name] : '' }}
					</slot>
				</div>
			</slot>
		</div>
	</div>
</template>

<script>
import { computed, defineComponent } from 'vue'

export default defineComponent({
	props: {
		color: {
			type: String,
			default: 'teal'
		},
		view: {
			type: String,
			default: 'smooth'
		},
		items: {
			type: Array,
			default: () => []
		},
		modelValue: {
			type: [Object, String, Number],
			default: () => ''
		},
		by: {
			type: String,
			default: 'value'
		},
		name: {
			type: String,
			default: 'name'
		},
		colorKey: {
			type: String,
			default: ''
		}
	},
	emits: ['update:modelValue', 'change'],
	setup(props, { emit }) {
		const items = computed(() => {
			return props.items.map((item) => {
				const classList = ['chip']
				const isSelected = props.by ? item[props.by] == props.modelValue : false
				if (isSelected) classList.push('--selected')
				if (!!props.view) classList.push('--smooth')
				if (!!props.colorKey) classList.push(`--${item[props.colorKey]}`)
				Object.assign(item, {
					classes: classList,
					selected: isSelected
				})
				return item
			})
		})
		const handleSelect = (value) => {
			emit('update:modelValue', value)
			emit('change', value)
		}
		return {
			items,
			handleSelect
		}
	}
})
</script>

<style lang="scss" scoped>
.chip {
	@apply flex items-center transition duration-200 px-4 py-2 rounded-full border border-grey-02 cursor-pointer hover:bg-grey-01 ml-2;
	&.--smooth {
		&.--selected {
			@apply border-teal-04 bg-teal-01 border-teal-04 text-teal-04;
			&.--orange {
				@apply border-orange-02 bg-orange-01 text-orange-04;
			}
			&.--green {
				@apply border-green-02 bg-green-01 text-green-04;
			}
			&.--red {
				@apply border-red-02 bg-red-01 text-red-04;
			}
		}
	}
}
</style>
